@import "../../../assets/theme/default/var.css";
.page-login{
  background: #eeeeee;
  margin:0;
}
@component-namespace template{
  @component login {
    margin-top:8%;
    @descendent header{
      margin:0 auto;
      text-align: center;
      font-size:24px;
    }
    @descendent box{
      width:350px;
      margin: 50px auto;
      background: var(--color-white);
      padding:15px 15px 20px;
    }
    @descendent title{
      text-align: center;
      font-size:18px;
      margin-bottom: 20px;
      font-weight: normal;
    }
    @descendent button{
      width:100%;
      margin-top:15px;
    }
    @descendent footer{
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
    }
    @descendent copyright{
      padding: 15px 0;
      text-align: center;
      font-size:12px;
      width: 100%;
      color: var(--color-extra-light-black);
    }
  }
}



